* {
  margin: 0;
    
}

#background-red {
  background-image: url("../../assets/background.jpg");
  background-size: 100%;
}

#happybirthday h1{
  font-size: 2.6rem;
  padding: 4rem;
  padding-top: 18rem;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: lighter;
}

#happybirthday p {
  display: block;
  max-width: 700px;
  padding-bottom: 10rem;
  line-height: 1.4;
  text-align: center;
}

#happybirthday p2 {
  display: block;
  max-width: 700px;
  padding-bottom: 15rem;
  line-height: 1.4;
  text-align: center;
}

#happybirthday body{
  display: block;
  text-align: center;
  background-color: #000000;
  font-size: 1rem;
  color: white;
  font-family: 'Menlo', 'Courier New', monospace;

}

